body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-align: center;
}
.container {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
p{
  font-size: 24px;

}
.btn{
  background-color: lightblue;
  border: 0px;
  width: 150px;
  height: 45px;
  border-radius: 20px;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
}